import { defineStore } from "pinia";

const useBaseStore = defineStore("BaseStore", {
  state: () => ({
    showSideMenu: false,
    showModal: false,
  }),
  getters: {},
  actions: {
    setSideMenu(value) {
      this.showSideMenu = value;
    },
    setModal(value) {
      this.showModal = value;
    },
  },
});

export default useBaseStore;
