import { createApp } from "vue";
import { createPinia } from "pinia";
import App from "./App.vue";
import router from "./router";
import Vue3TouchEvents from "vue3-touch-events";
// import store from './store';

createApp(App)
  .use(createPinia())
  .use(Vue3TouchEvents)
  .use(router)
  .mount("#app");
