<template>
  <div class="modal" :class="{ modalActive: showModal }">
    <div class="modal__window">
      <div class="modal__texts">
        <div class="modal__logo"></div>
        <div class="modal__caption">
          <div class="modal__captionPart">
            Feel the difference of professional digital promotion.
          </div>
          <div class="modal__captionPart">
            See the leads, get your customers and scale with
            <span class="modal__textAccent">Golden Gate</span>
            Professionals.
          </div>
        </div>
      </div>
      <div
        class="modal__form"
        :class="{ modal__form_success: dataHasBeenSent }"
      >
        <div class="modal__close" @click="hideModal()"></div>
        <div class="modal__formInner">
          <div class="modal__inputPart">
            <div class="modal__inputTitle modal__inputTitle_required">
              Name *
            </div>
            <div
              class="modal__input"
              :class="{ modal__input_error: isInputEmpty(name, 'name') }"
            >
              <input
                type="text"
                placeholder="Your name"
                v-model="name"
                @blur="onBlurInput('name')"
              />
            </div>
          </div>

          <div class="modal__inputPart">
            <div class="modal__inputTitle">Company</div>
            <div class="modal__input">
              <input
                class="error"
                type="text"
                placeholder="Company name"
                v-model="company"
              />
            </div>
          </div>

          <div class="modal__inputPart">
            <div class="modal__inputTitle modal__inputTitle_required">
              Email *
            </div>
            <div
              class="modal__input"
              :class="{
                modal__input_error: isInputEmpty(email, 'email'),
                modal__input_invalid: isEmailInvalid,
              }"
            >
              <input
                type="text"
                placeholder="Your email"
                v-model="email"
                @blur="onBlurInput('email')"
              />
            </div>
          </div>

          <div class="modal__inputPart">
            <div class="modal__inputTitle modal__inputTitle_required">
              Make an appointment *
            </div>
            <div
              class="modal__input"
              :class="{ modal__input_error: isInputEmpty(date, 'date') }"
            >
              <input
                type="date"
                placeholder="Choose day"
                v-model="date"
                @blur="onBlurInput('date')"
              />
            </div>
          </div>
        </div>

        <div class="modal__button">
          <div
            class="button"
            :class="{ button_disabled: !isButtonEnabled }"
            @click="formSubmit()"
          >
            send
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<!-- eslint-disable no-unused-vars -->

<script setup>
import { ref, computed } from "vue";
import { storeToRefs } from "pinia";
import useBaseStore from "@/store/BaseStore";

let alreadyFocused = ref([]);

const onBlurInput = (key) => {
  if (!alreadyFocused.value.includes(key)) alreadyFocused.value.push(key);
};

const isInputEmpty = (value, key) => {
  return value === "" && alreadyFocused.value.includes(key);
};

const baseStore = useBaseStore();
const { showModal } = storeToRefs(baseStore);

const hasLength = (word) => {
  return word.length;
};

const dataHasBeenSent = ref(false);
let name = ref("");
let email = ref("");
let company = ref("");
let date = ref("");

const isEmailInvalid = computed(() => {
  // eslint-disable-next-line no-control-regex, no-useless-escape, prettier/prettier
  const pattern = new RegExp("([!#-'*+/-9=?A-Z^-~-]+(\.[!#-'*+/-9=?A-Z^-~-]+)*|\"\(\[\]!#-[^-~ \t]|(\\[\t -~]))+\")@([!#-'*+/-9=?A-Z^-~-]+(\.[!#-'*+/-9=?A-Z^-~-]+)*|\[[\t -Z^-~]*])");
  return !pattern.test(email.value) && alreadyFocused.value.includes("email");
});

const isButtonEnabled = computed(() => {
  return (
    hasLength(name.value) &&
    hasLength(email.value) &&
    hasLength(date.value) &&
    !isEmailInvalid.value
  );
});
const aaa = computed(() => {
  return [
    hasLength(name.value),
    hasLength(email.value),
    hasLength(date.value),
    !isEmailInvalid.value,
  ];
});

const sendTo = process.env.VUE_APP_SEND_TO;
const token = process.env.VUE_APP_TOKEN;

console.log(process.env.VUE_APP_TOKEN);

const sendToTg = () => {
  if (isButtonEnabled.value) {
    const message = `Имя: ${name.value}; email: ${email.value}; компания: ${company.value}; дата: ${date.value}`;
    // const endpointUrl = `https://api.telegram.org/bot${token}/sendMessage?chat_id=${sendTo}&text=${message}`;
    const endpointUrl = `https://api.telegram.org/bot5319203164:${token}/sendMessage?chat_id=${sendTo}&text=${message}`;
    let xhr = new XMLHttpRequest();
    xhr.open("GET", endpointUrl);
    xhr.send();
  }
};

const hideModal = () => {
  baseStore.setModal(false);
  document.getElementsByTagName("html")[0].classList.remove("unableToScroll");
  dataHasBeenSent.value = false;
  name.value = "";
  email.value = "";
  company.value = "";
  date.value = "";
  alreadyFocused.value = [];
};

const hasValue = (value) => {
  return value.length > 0;
};

const formSubmit = () => {
  if (hasValue(name.value) && hasValue(email.value) && hasValue(date.value)) {
    dataHasBeenSent.value = true;

    sendToTg();
  }
};
</script>

