<template>
  <div class="header">
    <div class="header__logo" @click="goTo('Home')"></div>
    <div
      class="header__menu"
      :class="{ header__menu_active: showMenu }"
      v-touch:swipe.right="handleSwipeToClose()"
    >
      <div class="header__mainMenu">
        <div
          class="header__menuItem"
          @click="goTo('Home')"
          :class="{ header__menuItem_active: isHomePage() }"
        >
          Grow your business
        </div>
        <div
          class="header__menuItem"
          @click="goTo('About')"
          :class="{ header__menuItem_active: isPath('about') }"
        >
          About us
        </div>
        <div
          class="header__menuItem"
          @click="goTo('Contacts')"
          :class="{ header__menuItem_active: isPath('contacts') }"
        >
          Contacts
        </div>
        <!-- <div class="header__menuItem">Eng</div> -->
        <div class="header__menuItem">
          <ThemeButton />
        </div>
      </div>
      <div class="header__mobileSocials">
        <div class="header__mobileSocial">instagram</div>
        <div class="header__mobileSocial">facebook</div>
      </div>
    </div>
    <div
      class="header__mobileMenuToggler"
      @click="toggleMenu()"
      :class="{ header__mobileMenuToggler_close: showMenu }"
    ></div>
  </div>
</template>

<script setup>
import ThemeButton from "./ThemeButton.vue";
import { ref, computed, watch } from "vue";
import { useRoute, useRouter } from "vue-router";
const route = useRoute();
const router = useRouter();

const goTo = (name) => {
  router.push({ name });
  showMenu.value = false;
};

const currentRoute = computed(() => route.path);

const isPath = (name) => {
  return currentRoute.value.indexOf(name) != -1;
};

const isHomePage = () => {
  return currentRoute.value.length === 1;
};

let showMenu = ref(false);

const toggleMenu = () => {
  showMenu.value = !showMenu.value;
  showMenu.value === true
    ? document.getElementsByTagName("html")[0].classList.add("unableToScroll")
    : document.getElementsByTagName("html")[0].classList.remove("unableToScroll");
};

const handleSwipeToClose = () => {
  return function () {
    showMenu.value = false;
  };
};

watch(showMenu, () => {
  if (showMenu.value === false)
    document.getElementsByTagName("html")[0].classList.remove("unableToScroll");
});
</script>

